@import "./variables";
@import "./mixins";
@import "./normalize";


 .preview__display--footer .menu__list {
     list-style: none;
     display: -webkit-box;
     display: -ms-flexbox;
     display: flex;
     -webkit-box-orient: horizontal;
     -webkit-box-direction: normal;
         -ms-flex-direction: row;
             flex-direction: row;
     -webkit-box-pack: start;
         -ms-flex-pack: start;
             justify-content: flex-start;
     padding: 10px;
   }

   .menu__items {
     border-radius: 50%;
     margin-left: 10px;
     border: 2px solid $colorOption1-3;
   }

      .menu__links {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 25px;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      height: 25px;
      text-decoration: none;
      color: $colorOption1-1;
    }